import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { InputDate } from "../../../../components/helpers/field";
import { getTglSystem } from "../../../../components/helpers/function";
import Tabel from "../../../../components/helpers/tabel";
import LaporanBatalServiceExel from "./exel/LaporanBatalServiceExel";
import LaporanBatalServicePdf from "./pdf/LaporanBatalServicePdf";
import Loading from 'react-fullscreen-loading';

class HeadFormBatalService extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
      columns: [
        {
          dataField: "no_faktur_service",
          text: "No Service",
          footerAttrs: {
            colSpan: "8",
          },
          footer: (cell, row, num, index) => {
            return (
              <div>
                Total Transaksi : {this.props.dataservices.length}{" "}
              </div>
            );
          },
        },
        {
          dataField: "tgl_system",
          text: "Tgl Transaksi",
        },
        {
          dataField: "nama_customer",
          text: "Nama Customer",
        },

        {
          dataField: "alamat_customer",
          text: "Alamat",
        },
        {
          dataField: "kode_sales",
          text: "Sales",
        },
        {
          dataField: "detail_barang.deskripsi",
          text: "Deskripsi",
        },
        {
          dataField: "detail_barang.berat",
          text: "Berat",
          headerClasses: "text-right",
          formatter: (cell) => {
            return <div className="text-right">{cell.toFixed(2)}</div>;
          },
        },
        {
          dataField: "detail_barang.ongkos",
          text: "Ongkos",
          headerClasses: "text-right",
          formatter: (cell) => {
            return (
              <div className="text-right">{cell.toLocaleString("kr-KO")}</div>
            );
          },
        },
      ],
    };
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }

  exportPdf() {
    LaporanBatalServicePdf(this.props.dataservices)
  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }

  render() {
   
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off">
        <div className="row">
        <div className="col-lg-4">
            <Field
              id="tgl_awal"
              name="tgl_awal"
              component={InputDate}
              label="Tanggal Dari"
              type="text"
              selected={this.state.tgl_awal}
              onChange={(date) => this.setStartDate(date)}
              placeholder="Masukan Tanggal Dari"
            />
          </div>

          <div className="col-lg-4">
            <Field
              name="tgl_akhir"
              component={InputDate}
              type="text"
              selected={this.state.tgl_akhir}
              onChange={(date) => this.setLastDate(date)}
              label="Tanggal Akhir"
              placeholder="Masukan Tanggal Akhir"
            />
          </div>

          
          <div className="col-lg-4">
            <div className="text-right">
              <label>&nbsp;</label>
              <button
                type="submit"
                value="PDF"
                name="type_btn"
                className="btn btn-primary btn-block"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari
                  </>
                ) : (
                  "Cari Laporan"
                )}
              </button>
            </div>
          </div>
          <div className="col-lg-12">
            <Tabel
              keyField="no_hp"
              data={this.props.export ? this.props.dataservices : []}
              columns={this.state.columns}
              empty={true}
              textEmpty="Data Laporan Batal Service Kosong"
            />
          </div>
          <div className={this.props.export ? "col-lg-12" : "col-lg-12 d-none"}>
            <label htmlFor="">&nbsp;</label>
            <div className="row">
              <div className="col-lg-6">
                <button type="button" onClick={()=>this.exportPdf()} className="btn btn-warning btn-block"> Export PDF </button>
              </div>
              <div className="col-lg-6">
                <LaporanBatalServiceExel data={this.props.dataservices} />
              </div>
            </div>
          </div>
        </div>
        {this.props.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </form>
    );
  }
}

HeadFormBatalService = reduxForm({
  form: "HeadFormBatalService",
  enableReinitialize: true,
})(HeadFormBatalService);
// export default connect()(HeadFormBatalService);
export default connect((state) => {
  return {
    initialValues: {
      tgl_awal: getTglSystem(),
      tgl_akhir: getTglSystem(),
    },
  };
})(HeadFormBatalService);
